.app {
    height: 100vh;
    display: flex;
    color: white;
    background-color: #020230;
  }
  
  .main {
    width: 75%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #020230),
      url("https://i.ytimg.com/vi/yQbJRGs0fy8/maxresdefault.jpg") center;
    display: flex;
    flex-direction: column;
  }
  
  .pyramid {
    width: 25%;
    background-color: #020230;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .moneyList {
    padding: 20px;
    list-style: none;
    width: 100%;
  }
  
  .moneyListItem {
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 5px;
  }
  
  .moneyListItem.active {
    background-color: teal;
  }
  
  .moneyListItemNumber {
    width: 30%;
    font-size: 18px;
    font-weight: 100;
  }
  
  .moneyListItemAmount {
    font-size: 20px;
    font-weight: 300;
  }

  .top{
      height: 50%;
      position: relative;
  }

  .timer{
      width:70px;
      height: 70px;
      border-radius: 50%;
      border:5px solid white;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 30px;
      font-weight: 700;
      position: absolute;
      bottom:10px;
      left:80px;
  }

  .bottom{
      height: 50%;
  }

  .trivia{
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
  }

  .question {
    width: 80%;
    background: linear-gradient(#100241, black);
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    border: 2px solid white;
    font-size: 20px;
  }

  .answers{
      width: 100%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;

  }

  .answer{
      width: 40%;
      padding:10px;
      text-align: center;
      background: linear-gradient(#0e0124, #22074d);
        border: 1px solid white;
        font-size: 20px;
        font-weight: 300;
        border-radius: 15px;
        margin: 0 10px 20px 10px;
        cursor: pointer;
  }

  .answer:hover , .answer.active{
      background: mediumblue;
  }

  @keyframes correct{
      0%,22%,42%{
          background: mediumblue;
        }
        20%,40%,60%{
            background: linear-gradient(#0e0124, #22074d);

        }

        62% , 100%{
            background:green;
        }
  }

  @keyframes wrong{
    0%,22%,42%{
        background: mediumblue;
      }
      20%,40%,60%{
          background: linear-gradient(#0e0124, #22074d);

      }

      62% , 100%{
          background:red;
      }

    }

    .answer.wrong{
        animation: wrong 3s ease forwards;
    }

  .answer.correct{
      animation: correct 3s ease forwards;
  }

  .endText{
      position: relative;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
  }

  .start{
      width: 250px;
      height: 100px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      position: relative;
      top:0;
      bottom:0;
      left:0;
      right:0;
      margin:auto;

  }

  .startInput{
      width: 100%;
      height: 30px;
      border:none;
      border-radius: 5px;
      text-align: center;
      font-size: 18px;

  }

  .startInput:focus{
      outline: none;
  }

  .startButton{
      width: 100%;
      height: 30px;
      border:none;
      border-radius: 5px;
      cursor: pointer;
      font-size: 18px;
      font-weight: 500;
  }